a {
	color: #fff;
}

/*---------------------*/
.header_fixed_wrapper_center {
	width: 100%;
}

.header_fixed_wrapper_left {
	width: 35%;
	min-width: 435px;
}

.header_container {
	position: fixed;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	height: 100vh;
	background-image: -webkit-linear-gradient(315deg, #65379b 0%, #886aea 53%, #6457c6 100%);
	background-image: -o-linear-gradient(315deg, #65379b 0%, #886aea 53%, #6457c6 100%);
	background-image: linear-gradient(-225deg, #65379b 0%, #886aea 53%, #6457c6 100%);
	color: #fff;
	-webkit-transition: all 0.5s ease-in-out;
	-o-transition: all 0.5s ease-in-out;
	transition: all 0.5s ease-in-out;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.header_container_center {
	-ms-flex-align: center;
	    align-items: center;
	width: 100%;
}

.header_container_left {
	-ms-flex-align: start;
	    align-items: flex-start;
	padding: 0 2rem;
	width: 35%;
	min-width: 435px;
	opacity: 1;
}

/*---------------------*/

.header_subheading_center {
	padding: 1rem 20rem;
	text-align: center;
}

.header_subheading_left {
	padding: 1rem 0;
	text-align: left;
}

/*---------------------*/

.icons_nav {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.icons_nav_center {
	-ms-flex-align: center;
	    align-items: center;
	padding: 0 0 1rem;
}

.icons_nav_left {
	-ms-flex-align: start;
	    align-items: flex-start;
	padding: 0 0 1rem;
}

.icons_nav_item {
	margin: 0.5rem;
	border: 2px solid #fff;
	border-radius: 60%;
	background-color: #fff;
	-webkit-transition: all 0.3s ease-in-out;
	-o-transition: all 0.3s ease-in-out;
	transition: all 0.3s ease-in-out;
}

.icons_nav_desktop,
.icons_nav_mobile {
	display: none;
}

.icons_nav_item:hover {
	background-color: transparent;
	color: #fff !important;
}

/*---------------------*/

button.link_to_works {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	margin-top: 1rem;
	padding: 0.5rem 1rem;
	text-align: center;
	color: #65379b;
	background-color: #fff;
	border: 3px solid #fff;
	-webkit-transition: border-radius 0.3s ease-in-out, background-color 0.3s ease-in-out;
	-o-transition: border-radius 0.3s ease-in-out, background-color 0.3s ease-in-out;
	transition: border-radius 0.3s ease-in-out, background-color 0.3s ease-in-out;
	cursor: pointer;
}

.link_to_works > .fa-angle-right {
	padding: 0 0.2rem;
}

.link_to_works:hover {
	color: #fff;
	background-color: transparent;
	border-radius: 2rem;
}

.return_arrow {
	display: none;
	cursor: pointer;
}

/*---------------------*/

/*- ipad pro screen size -*/
@media all and (max-width: 1024px) {
	.header_subheading_center {
		padding: 1rem 5rem;
	}
}

/*- ipad screen size -*/
@media all and (max-width: 768px) {
	.header_fixed_wrapper_left {
		position: fixed;
		bottom: 0;
		width: 100%;
		min-width: 0;
		z-index: 2;
	}
	.header_container_left {
		display: none;
	}
	.icons_nav_mobile {
		display: -ms-flexbox;
		display: flex;
		background-image: -webkit-linear-gradient(315deg, #65379b 0%, #886aea 53%, #6457c6 100%);
		background-image: -o-linear-gradient(315deg, #65379b 0%, #886aea 53%, #6457c6 100%);
		background-image: linear-gradient(-225deg, #65379b 0%, #886aea 53%, #6457c6 100%);
		-webkit-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
	}
	.icons_nav.icons_nav_mobile {
		padding: 0;
	}
	.return_arrow {
		margin: 1rem;
		padding: 1.5rem 1.63rem;
		color: #65379b;
		background-color: #fff;
		border-radius: 60%;
		-webkit-box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, 0.2);
		        box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, 0.2);
		-webkit-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
	}
	.return_arrow_mobile_view {
		display: inline-block;
	}
	.return_arrow_mobile_hide {
		display: none;
	}
	.fade {
		-webkit-transition: all 0.3s ease-in-out;
		-o-transition: all 0.3s ease-in-out;
		transition: all 0.3s ease-in-out;
	}
}

/*- mobile screen size -*/
@media all and (max-width: 500px) {
	.header_subheading_center {
		padding: 0rem 2rem;
	}
	.icons_nav {
		padding: 0 0 1rem;
	}
}



